const API_KEY = "68d3c74a18d32691083dc9e313e197f5";
const BASE_URL = "https://api.themoviedb.org/3/";

function fetchMoviesTrending() {
  return fetch(`${BASE_URL}trending/movie/day?api_key=${API_KEY}`).then(
    (resp) => {
      if (resp.ok) {
        return resp.json();
      }
      return Promise.reject(new Error(`Popular movies failed to load`));
    }
  );
}
function fetchMovies(query) {
  return fetch(
    `${BASE_URL}search/movie?api_key=${API_KEY}&language=en-US&query=${query}&page=1&include_adult=true`
  ).then((resp) => {
    if (resp.ok) {
      return resp.json();
    }
    return Promise.reject(
      new Error(`Not found any movies by query: "${query}"`)
    );
  });
}
function fetchMoviesDetails(id) {
  return fetch(`${BASE_URL}movie/${id}?api_key=${API_KEY}&language=en-US`).then(
    (resp) => {
      if (resp.ok) {
        return resp.json();
      }
      return Promise.reject(
        new Error(`Not found any movies by identifier: '${id}'`)
      );
    }
  );
}

function fetchMoviesCast(id) {
  return fetch(
    `${BASE_URL}movie/${id}/credits?api_key=${API_KEY}&language=en-US`
  ).then((resp) => {
    if (resp.ok) {
      return resp.json();
    }
    return Promise.reject(
      new Error(`Not found any cast by identifier: '${id}'`)
    );
  });
}
function fetchMoviesReviews(id, page = 1) {
  return fetch(
    `${BASE_URL}movie/${id}/reviews?api_key=${API_KEY}&language=en-Us&page=${page}`
  ).then((resp) => {
    if (resp.ok) {
      return resp.json();
    }
    return Promise.reject(
      new Error(`Not found any movies reviews by identifier: '${id}'`)
    );
  });
}
const api = {
  fetchMoviesTrending,
  fetchMovies,
  fetchMoviesDetails,
  fetchMoviesCast,
  fetchMoviesReviews,
};
export default api;
